body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/**
* light color gray
* dark color green
* border color black
*/
:root
{
	--light-color: #ccc;
	--dark-color: #378a24;
	--border-color: #000;
}
/**
*
*/

/* board size variables */
:root
{
	--cell-size: 64px;
	--border-size: 10px;
	--board-size: 512px;
}

/* border wrapper setup */
.board_border 
{
	float: left;
	width: 512px;
	width: var(--board-size); 
	height: 512px; 
	height: var(--board-size); 
	border: solid var(--border-color) 10px; 
	border: solid var(--border-color) var(--border-size);
}

/* actual board */
table#t01
{
	border-collapse: collapse;
}
table#t01 td
{
	width: 64px;
	width: var(--cell-size);
	height: 64px;
	height: var(--cell-size);
	padding: 0;
}
table#t01 tr:nth-child(odd) td:nth-child(even) { background: var(--dark-color); }
table#t01 tr:nth-child(even) td:nth-child(odd) { background: var(--dark-color); }
table#t01 tr:nth-child(even) td:nth-child(even) { background: var(--light-color); }
table#t01 tr:nth-child(odd) td:nth-child(odd) { background: var(--light-color); }

